import Graph from "graphology"
import { ForensicResponse } from "../types/forensic"

export function buildGraph(data: ForensicResponse) {
  const graph = new Graph()

  const suspiciousMap = new Map(
    data.suspicious_accounts.map(a => [a.account_id, a])
  )

  const allAccounts = new Set<string>()

  data.fraud_rings.forEach(ring => {
    ring.member_accounts.forEach(a => allAccounts.add(a))
  })

  const accounts = Array.from(allAccounts)

  const radius = 10

  accounts.forEach((account, i) => {
    const angle = (i / accounts.length) * 2 * Math.PI

    const suspicion = suspiciousMap.get(account)?.suspicion_score || 0

    graph.addNode(account, {
      label: account,
      size: 6 + suspicion / 10,
      color: getColor(suspicion),
      x: Math.cos(angle) * radius,
      y: Math.sin(angle) * radius
    })
  })

  // Connect ring members in circular fashion
  data.fraud_rings.forEach(ring => {
    const members = ring.member_accounts
    for (let i = 0; i < members.length; i++) {
      const a = members[i]
      const b = members[(i + 1) % members.length]

      graph.addEdge(a, b, { color: "#ccc" })
    }
  })

  return graph
}

function getColor(score: number) {
  if (score > 80) return "#ff0000"
  if (score > 60) return "#ff6600"
  if (score > 40) return "#ffaa00"
  return "#ffd700"
}
